import { apis, commonParams, option } from 'api/config';
import jsonp from 'common/js/jsonp'
import axios from 'axios'
/**
 * [getRecommend 获取轮播图]
 */
export function getRecommend() {
  let url = apis.recommend;
  let data = Object.assign({}, commonParams, {
    platform: 'h5',
    needNewCode: 1,
    uin: 0
  });
  return jsonp(url, data, option);
}
/**
 * [getDiscList 获取歌单列表]
 * @param {number:28} categoryId [歌单id]
 */
export function getDiscList(categoryId = 28) {
  const url = './static/disclist.json'

  return axios.get(url).then((res) => {
    return Promise.resolve(res.data)
  })
}
/**
 * @description: [getSingList 获取歌手列表]
 * @param : {number} {-100} area [地区]
 * @param : {number} {-100} sex [性别]
 * @param : {number} {-100} genre [类型]
 * @param : {number} {-100} index [字母序号]
 * @return: {type} name [desc]
 */
export function getSingerList({ area = -100, sex = -100, genre = -100, index = -100 } = {}) {
  let url = './static/singerlist.json';

  return axios.get(url).then(res => {
    return Promise.resolve(res.data);
  })
}
/**
 *
 * @param {number} id 歌手id
 */
export function getSingerSongList(id) {
  let url = apis.singerSongList;
  let data = Object.assign({}, commonParams, {
    '-': 'getUCGI05770517354052007',
    platform: 'yqq.json',
    needNewCode: 0,
    uin: 0,
    loginUin: 0,
    hostUin: 0,
    format: 'json',
    data: { "comm": { "ct": 24, "cv": 0 }, "singer": { "method": "get_singer_detail_info", "param": { "sort": 5, "singermid": id, "sin": 0, "num": 10 }, "module": "music.web_singer_info_svr" } }
  });
  return axios.get(url, {
    params: data
  }).then(res => {
    console.log('getSingerSongList' + res);
    return Promise.resolve(res.data);
  })
}
/**
 *
 * @param {number} id 歌单id
 */
export function getDiscSongList(id) {
  let url = './static/singerlist.json';
  return axios.get(url).then(res => {
    return Promise.resolve(res.data);
  })
}
/**
 * [getAllData 获取所有数据]
 */
export function getAllData() {
  let url = './static/alldata.json';
  return axios.get(url).then(res => {
    return Promise.resolve(res.data);
  })
}
/**
 *
 * @param {number} id 歌单id
 * @return {singer} url 播放地址
 */
export function getSongUrl(id) {
  let url = apis.recommend;
  let data = Object.assign({}, commonParams, {
    '-': 'getplaysongvkey9175483571044594',
    g_tk: 5381,
    hostUin: 0,
    format: 'json',
    platform: 'yqq.json',
    needNewCode: 0,
    data: { "req_0": { "module": "vkey.GetVkeyServer", "method": "CgiGetVkey", "param": { "guid": "745412736", "songmid": [id], "songtype": [0], "uin": "3282878593", "loginflag": 1, "platform": "20" } }, "comm": { "uin": 3282878593, "format": "json", "ct": 24, "cv": 0 } }
  });
  return axios.get(url, {
    params: data
  }).then(res => {
    return Promise.resolve(res.data);
  });
}

export function getSearch(keyWord) {
  let url = apis.search;
  let data = Object.assign({}, commonParams, {
    is_xml: 0,
    key: keyWord,
    g_tk: 5381,
    loginUin: 3282878593,
    hostUin: 0,
    format: 'json',
    notice: 0,
    platform: 'yqq.json',
    needNewCode: 0
  });
  return axios.get(url, {
    params: data
  }).then(res => {
    return Promise.resolve(res.data);
  });
}
export function getApi() {
  const url = 'https://api.apiopen.top/getJoke?type=all';
  return axios.get(url).then(res => {
    return Promise.resolve(res.data);
  })
}
